/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  height: 100vh;
  font-family: 'Arial', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 16px;
}

h2 {
  color: #fff;
  margin-bottom: 20px;
}

/* Login Form Styles */
.login-container {
  width: 100%;
  max-width: 400px;
  background: rgba(0, 0, 0, 0.6); /* Dark background with transparency */
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px); /* Adds a blur effect to the background */
  text-align: center;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
  color: #ccc;
  margin-bottom: 5px;
}

input {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #ddd;
  margin-top: 8px;
  background-color: #fff;
  color: #333;
  outline: none;
  transition: all 0.3s ease;
}

input:focus {
  border-color: #2575fc;
  box-shadow: 0 0 10px rgba(37, 117, 252, 0.5);
}

input::placeholder {
  color: #aaa;
}

/* Error Message */
.error-message {
  color: #ff4d4d;
  font-size: 14px;
  margin-bottom: 15px;
  font-weight: bold;
}

/* Submit Button */
.login-button {
  padding: 15px;
  background-color: #2575fc;
  color: white;
  font-size: 18px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.login-button:hover {
  background-color: #1a61c3;
  transform: translateY(-4px);
}

.login-button:active {
  transform: translateY(2px);
}

/* Add animation to the form */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-container {
  animation: fadeIn 0.8s ease-out;
}

/* Adding some responsive styles */
@media screen and (max-width: 480px) {
  .login-container {
    padding: 20px;
  }

  h2 {
    font-size: 24px;
  }

  .login-button {
    font-size: 16px;
  }
}
